<template>
    <div>
        <div class="panel panel-info">
            <div class="panel-body">
                <div>功能管理</div>
                <div class="buttonbox" v-show="!inforadmin">
                    <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="hasLoginComponents('sys:page:add')">新增</button>
                    <button type="button" class="btn btn-warning" @click="updateData" v-if="hasLoginComponents('sys:page:modify')">修改</button>
                    <button type="button" class="btn btn-danger" @click="deleteData" v-if="hasLoginComponents('sys:page:delete')">删除</button>
                    <div class="pull-right col-sm-3 search-input" v-if="hasLoginComponents('sys:page:search')">
                        <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                        <input type="text" class="form-control" placeholder="搜索用户"> 
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                        </div>
                    </div>
                </div>
                <tables @logintable="logintable" v-show="!inforadmin" @tableCheck="onTableClick" :pageloginData="pageloginData"></tables>
                <add-infor  v-show="inforadmin" :tableData="tableData" :opreate="opreate" :statetitle="statetitle" :stateId="stateid" :checkData="checkData" @inforCall="inforAddClick"></add-infor>
            </div>
        </div>
    </div>
</template>
<script>
import tables from "@/components/table.vue";
import addInfor from "@/components/addinfor.vue";
export default {
  data: function() {
    return {
      checkData: [],
      inforadmin: false,
      searchData: { data: "" },
      pageloginData: {
        id: this.stateid
      },
      opreate: "add",
      tableData: function() {}
    };
  },
  props: {
    stateid: String,
    statetitle: String
  },
  components: {
    tables,
    addInfor
  },
  methods: {
    logintable(func) {
      this.tableData = func;
      this.tableData();
    },
    onTableClick(data) {
      this.checkData = data;
    },
    //判断是否当前页面是否存在按钮权限
    hasLoginComponents(perm) {
      var vm = this;
      return vm.$api.opreate.hasLoginComponents(vm, perm);
    },
    inforAddClick(bool) {
      this.inforadmin = bool;
      this.opreate = "add";
    },
    // 修改按钮按下
    updateData() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm, "修改")) {
        this.inforadmin = true;
        this.opreate = "update";
      }
    },
    // 删除按钮按下
    deleteData() {
      var vm = this;
      var checkData = vm.checkData;
      if (vm.$api.opreate.TESTcheck(vm, "删除")) {
         //询问框
        layer.confirm(
          "是否删除",
          {
            btn: ["确认", "取消"] //按钮
          },
          function() {
            var datas = {
              id: checkData[0].id,
              states: {
                id: vm.stateid
              }
            };
            vm.$api.http.opreateHttp(vm, "deleteData.action", datas, "删除",vm.tableData);
          }
        );
      }
    },
    //搜索按钮按下
    searchBtnClick() {
      var vm = this;
      if (vm.searchData.data != "") {
      } else {
        vm.$layer.msg("请输入用户名后搜索",{icon:0});
      }
    }
  },
  watch: {
    stateid(news) {
      this.pageloginData.id = news;
    }
  }
};
</script>

<style>
</style>



